'use client';

import { useState, useEffect, useRef } from 'react';
import Card from '../components/Card';
import { MISSION_LOGBOOK } from '../data/missions'; // Assure-toi que ce fichier existe

// --- TYPES ---
type CardData = {
  id: string; 
  color: 'Blue' | 'Green' | 'Pink' | 'Yellow' | 'Rocket';
  value: number;
  owner: number;
  status: 'HAND' | 'TABLE' | 'WON';
  zIndex: number;
  tableRotation: number;
  winnerIndex?: number;
};

type MoveStep = {
    player: number;
    card: { color: string, value: number };
};

type SolverStats = {
  solution_steps: MoveStep[];
  solutionFound: boolean;
  iterations: number;
};

type MissionUI = {
    cardColor: string;
    cardValue: number;
    ownerIndex: number; // 0=Sud, 1=Ouest, 2=Nord, 3=Est
    token: string; 
};

// --- CONFIGURATION ---
const ANIMATION_SPEED = 600; 
const TRICK_WAIT_ON_TABLE = 1000; 

// --- COMPOSANT D'AFFICHAGE DES PLIS ---
const TrickHistory = ({ steps }: { steps: MoveStep[] }) => {
    // On d√©coupe les 40 coups en 10 plis de 4 cartes
    const tricks = [];
    for (let i = 0; i < steps.length; i += 4) {
        tricks.push(steps.slice(i, i + 4));
    }

    // Petite fonction pour trouver le gagnant d'un pli (logique simplifi√©e pour l'affichage)
    const getWinnerIndex = (trick: MoveStep[]) => {
        // Cette logique est purement visuelle, le vrai gagnant est calcul√© par le backend
        // Mais on peut le d√©duire : Le backend renvoie les coups dans l'ordre.
        // Le gagnant du pli N commence le pli N+1.
        // Donc le joueur qui joue la 1√®re carte du pli suivant EST le gagnant de ce pli.
        // Sauf pour le dernier pli... 
        // Pour faire simple ici, on affiche juste les cartes jou√©es.
        return trick[0].player; // Celui qui entame (visuel simple)
    };

    const getPlayerName = (idx: number) => ['Sud', 'Ouest', 'Nord', 'Est'][idx];
    
    // Couleurs CSS pour les badges
    const getColorClass = (color: string) => {
        switch(color) {
            case 'Blue': return 'bg-blue-900 text-blue-200 border-blue-700';
            case 'Green': return 'bg-green-900 text-green-200 border-green-700';
            case 'Pink': return 'bg-pink-900 text-pink-200 border-pink-700';
            case 'Yellow': return 'bg-yellow-900 text-yellow-200 border-yellow-700';
            case 'Rocket': return 'bg-gray-700 text-white border-gray-500';
            default: return 'bg-gray-800';
        }
    };

    return (
        <div className="space-y-2 mt-4">
            <h3 className="text-xs font-bold text-gray-400 uppercase border-b border-gray-700 pb-1">
                üìù D√©tail de la solution
            </h3>
            {tricks.map((trick, i) => (
                <div key={i} className="bg-black/30 p-2 rounded border border-white/5 text-[10px]">
                    <div className="flex justify-between mb-1 text-gray-400 font-mono">
                        <span>Pli #{i + 1}</span>
                        <span>Entame: {getPlayerName(trick[0].player)}</span>
                    </div>
                    <div className="grid grid-cols-4 gap-1">
                        {trick.map((move, j) => (
                            <div key={j} className="flex flex-col items-center">
                                <span className="text-[8px] text-gray-500 mb-0.5">{getPlayerName(move.player).substring(0,1)}</span>
                                <div className={`w-full text-center py-0.5 rounded border ${getColorClass(move.card.color)} font-bold`}>
                                    {move.card.value}
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            ))}
        </div>
    );
};

// Helper pour transformer le JSON du backend en format CardData pour React
const convertBackendToFrontend = (gameData: any): CardData[] => {
    const generatedCards: CardData[] = [];
    ['player_1', 'player_2', 'player_3', 'player_4'].forEach((key, playerIdx) => {
        gameData[key].forEach((c: any) => {
            const seed = c.value * 100 + playerIdx * 50 + c.color.length;
            generatedCards.push({
                id: `${c.color}-${c.value}`,
                color: c.color,
                value: c.value,
                owner: playerIdx,
                status: 'HAND',
                zIndex: 0,
                tableRotation: (seed % 40) - 20,
                winnerIndex: undefined
            });
        });
    });
    return generatedCards;
};

export default function Home() {
  const [allCards, setAllCards] = useState<CardData[]>([]);
  const [stats, setStats] = useState<SolverStats | null>(null);
  
  // Controls
  const [currentMoveIndex, setCurrentMoveIndex] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [isThinking, setIsThinking] = useState(false);
  const [logs, setLogs] = useState<string[]>([]);
  
  // --- ETATS POUR LES MISSIONS ---
  const [missions, setMissions] = useState<MissionUI[]>([]);
  const [bookMissionId, setBookMissionId] = useState<number>(0);
  const [isAutoFinding, setIsAutoFinding] = useState(false);

  const timerRef = useRef<NodeJS.Timeout | null>(null);

  const abortControllerRef = useRef<AbortController | null>(null);

  const addLog = (msg: string) => setLogs(prev => [`> ${msg}`, ...prev]);

  useEffect(() => { startNewGame(); }, []);

  // --- MOTEUR DE JEU (AUTO PLAY) ---
  useEffect(() => {
    if (!isPlaying || !stats) return;

    const playNext = () => {
        if (currentMoveIndex >= stats.solution_steps.length) {
            setIsPlaying(false);
            return;
        }

        let nextStep = 0;
        let delay = ANIMATION_SPEED;

        // Est-ce qu'on est sur une √©tape "Ramassage" (X.5) ?
        if (currentMoveIndex % 1 !== 0) {
            nextStep = Math.floor(currentMoveIndex) + 1;
            delay = ANIMATION_SPEED;
        } 
        // Est-ce une fin de pli (4, 8, 12...) sur la table (Entier) ?
        else if ((currentMoveIndex + 1) % 4 === 0) {
            nextStep = currentMoveIndex + 0.5;
            delay = TRICK_WAIT_ON_TABLE;
        } 
        else {
            nextStep = currentMoveIndex + 1;
            delay = ANIMATION_SPEED;
        }

        goToStep(nextStep);
        timerRef.current = setTimeout(playNext, delay);
    };

    timerRef.current = setTimeout(playNext, ANIMATION_SPEED);

    return () => { if (timerRef.current) clearTimeout(timerRef.current); };
  }, [isPlaying, currentMoveIndex, stats]);

  // --- GESTION CLAVIER ---
  useEffect(() => {
      const handleKeyDown = (e: KeyboardEvent) => {
          // Si on est en train de taper dans un champ, on ignore
          if (['INPUT', 'SELECT', 'TEXTAREA'].includes((e.target as HTMLElement).tagName)) return;

          if (e.key === 'ArrowRight') handleNext();
          if (e.key === 'ArrowLeft') handlePrev();
          if (e.key === ' ') {
              e.preventDefault(); // Emp√™che le scroll
              setIsPlaying(prev => !prev);
          }
      };

      window.addEventListener('keydown', handleKeyDown);
      return () => window.removeEventListener('keydown', handleKeyDown);
  }, [currentMoveIndex, stats]); // D√©pendances importantes pour avoir les valeurs √† jour


  // --- CALCUL DE POSITION (L'√âVENTAIL) ---
  const getCardStyle = (card: CardData) => {
      // 1. CARTE GAGN√âE
      if (card.status === 'WON') {
          const winPos = [
              "translate(-50%, 800px)", // Sud
              "translate(-1200px, -50%)", // Ouest
              "translate(-50%, -1000px)", // Nord
              "translate(1200px, -50%)" // Est
          ];
          return {
              transform: winPos[card.winnerIndex || 0] + " scale(0.4)",
              opacity: 0,
              zIndex: card.zIndex
          };
      }

      // 2. CARTE SUR TABLE
      if (card.status === 'TABLE') {
          const seed = card.tableRotation; 
          const rX = (seed * 5) % 20 - 10;
          const rY = (seed * 7) % 20 - 10;
          
          return {
              transform: `translate(calc(-50% + ${rX}px), calc(-50% + ${rY}px)) rotate(${card.tableRotation}deg) scale(1.1)`,
              opacity: 1,
              zIndex: card.zIndex
          };
      }

      // 3. CARTE EN MAIN
      const cardsInHand = allCards.filter(c => c.owner === card.owner && c.status === 'HAND');
      
      // Tri par Couleur puis Valeur
      const colorPriority: Record<string, number> = { 'Blue': 1, 'Green': 2, 'Pink': 3, 'Yellow': 4, 'Rocket': 5 };
      cardsInHand.sort((a, b) => {
          if (a.color !== b.color) return colorPriority[a.color] - colorPriority[b.color];
          return a.value - b.value;
      });
      
      const myIndex = cardsInHand.findIndex(c => c.id === card.id);
      const total = cardsInHand.length;
      
      const FIXED_ANGLE = 6; 
      const xSpacing = 4.5; 
      const yArch = 0.03; 

      const totalSpread = (total - 1) * FIXED_ANGLE;
      const rotation = (myIndex * FIXED_ANGLE) - (totalSpread / 2);
      
      const xOffset = rotation * xSpacing; 
      const yOffset = (rotation * rotation) * yArch; 

      let baseTransform = "";
      
      switch(card.owner) {
        case 0: // SUD
            baseTransform = `translate(calc(-50% + ${xOffset}px), calc(200px + ${yOffset}px)) rotate(${rotation}deg)`;
            break;
        case 1: // OUEST
            baseTransform = `translate(calc(-540px - ${yOffset}px), calc(-50% + ${xOffset}px)) rotate(${90 + rotation}deg)`;
            break;
        case 2: // NORD
            baseTransform = `translate(calc(-50% - ${xOffset}px), calc(-320px - ${yOffset}px)) rotate(${180 + rotation}deg)`;
            break;
        case 3: // EST
            baseTransform = `translate(calc(450px + ${yOffset}px), calc(-50% - ${xOffset}px)) rotate(${-90 + rotation}deg)`;
            break;
      }

      return {
          transform: baseTransform,
          opacity: 1,
          zIndex: myIndex
      };
  };

  // --- LOGIQUE ETAPES ---
  const goToStep = (targetStep: number) => {
      if (!stats) return;
      
      const target = Math.max(0, Math.min(targetStep, stats.solution_steps.length));
      const floorStep = Math.floor(target);
      const isCollectionPhase = target % 1 !== 0;

      // Reset
      const newCards: CardData[] = allCards.map(c => ({
          ...c, status: 'HAND', zIndex: 0, winnerIndex: undefined
      }));

      for (let i = 0; i <= floorStep; i++) {
          if (i >= stats.solution_steps.length) break;

          const move = stats.solution_steps[i];
          const cIdx = newCards.findIndex(c => c.id === `${move.card.color}-${move.card.value}`);
          
          if (cIdx !== -1) {
              newCards[cIdx].status = 'TABLE';
              newCards[cIdx].zIndex = 100 + (i % 4); 
          }

          if ((i + 1) % 4 === 0) {
              const isCurrentStep = (i === floorStep);
              if (!isCurrentStep || (isCurrentStep && isCollectionPhase)) {
                  const trickMoves = stats.solution_steps.slice(i - 3, i + 1);
                  const winner = getTrickWinner(trickMoves);
                  const trickIds = trickMoves.map(m => `${m.card.color}-${m.card.value}`);
                  
                  trickIds.forEach(tid => {
                      const idx = newCards.findIndex(c => c.id === tid);
                      if (idx !== -1) {
                          newCards[idx].status = 'WON';
                          newCards[idx].winnerIndex = winner;
                      }
                  });
              }
          }
      }
      setAllCards(newCards);
      setCurrentMoveIndex(target);
  };

  // --- FONCTIONS DE MELANGE ---

  // 1. M√©langer UNIQUEMENT les propri√©taires (Qui fait la mission ?)
  const shuffleOwners = () => {
      setMissions(prev => prev.map(m => ({
          ...m,
          // Choisit un joueur au hasard entre 0 et 3
          ownerIndex: Math.floor(Math.random() * 4)
      })));
  };

  // 2. M√©langer UNIQUEMENT les cartes (Quelle est la mission ?)
  const shuffleCards = () => {
      if (allCards.length === 0) return;

      // On r√©cup√®re toutes les cartes valides pour des missions (Pas de fus√©es)
      const validTaskCards = allCards.filter(c => c.color !== 'Rocket');
      
      // On m√©lange ce paquet
      const shuffled = [...validTaskCards].sort(() => Math.random() - 0.5);

      // On met √† jour chaque mission avec une nouvelle carte du paquet m√©lang√©
      setMissions(prev => prev.map((m, i) => {
          // S√©curit√© : s'il y a plus de missions que de cartes disponibles
          if (!shuffled[i]) return m; 
          
          return {
              ...m,
              cardColor: shuffled[i].color,
              cardValue: shuffled[i].value
          };
      }));
  };

  const loadMissionFromBook = (id: number) => {
      setBookMissionId(id);
      const missionDef = MISSION_LOGBOOK.find(m => m.id === id);
      if (!missionDef) return;

      // S√©curit√© : Si pas de cartes distribu√©es, on ne peut pas g√©n√©rer de missions al√©atoires
      if (allCards.length === 0) {
          addLog("‚ö†Ô∏è Distribuez d'abord les cartes !");
          return;
      }

      // 1. Trouver les cartes √©ligibles (Couleurs 1-9, PAS DE FUSEES)
      const validTaskCards = allCards.filter(c => c.color !== 'Rocket');

      // 2. M√©langer al√©atoirement le paquet de t√¢ches
      // (Algorithme de tri al√©atoire simple)
      const shuffledTasks = [...validTaskCards].sort(() => Math.random() - 0.5);

      // 3. Identifier le Commandant (Celui qui a le 4 Fus√©e)
      // C'est lui qui re√ßoit la premi√®re mission par d√©faut dans cette version auto
      const commander = allCards.find(c => c.color === 'Rocket' && c.value === 4);
      const startPlayerIdx = commander ? commander.owner : 0; // Par d√©faut Sud si pas trouv√©

      // 4. Cr√©er les missions en distribuant de mani√®re horaire
      const newMissions: MissionUI[] = missionDef.slots.map((slot, index) => {
          // On prend la carte suivante dans le paquet m√©lang√©
          const targetCard = shuffledTasks[index];
          
          // Distribution horaire : (Commandant + 0, Commandant + 1...) % 4
          const assignedOwner = (startPlayerIdx + index) % 4;

          return {
              cardColor: targetCard.color,
              cardValue: targetCard.value,
              ownerIndex: assignedOwner,
              token: slot.token
          };
      });

      setMissions(newMissions);
      addLog(`Mission ${id} charg√©e : ${missionDef.description}`);
  };

  const removeMission = (index: number) => {
      setMissions(prev => prev.filter((_, i) => i !== index));
  };

  const getPlayerName = (idx: number) => ['Sud', 'Ouest', 'Nord', 'Est'][idx];

  // --- ACTIONS JEU ---
  const handleNext = () => {
      setIsPlaying(false);
      if (Number.isInteger(currentMoveIndex) && (currentMoveIndex + 1) % 4 === 0) {
          goToStep(currentMoveIndex + 0.5);
      } else if (currentMoveIndex % 1 !== 0) {
           goToStep(Math.floor(currentMoveIndex) + 1);
      } else {
          goToStep(currentMoveIndex + 1);
      }
  };

  const handlePrev = () => {
      setIsPlaying(false);
      if (currentMoveIndex % 1 !== 0) {
          goToStep(Math.floor(currentMoveIndex));
      } else {
          const prevIndex = currentMoveIndex - 1;
          if (prevIndex >= 0 && (prevIndex + 1) % 4 === 0) {
              goToStep(prevIndex + 0.5); 
          } else {
              goToStep(prevIndex);
          }
      }
  };

  const getTrickWinner = (moves: MoveStep[]) => {
    const leadColor = moves[0].card.color;
    let bestMove = moves[0];
    for (let i = 1; i < 4; i++) {
        const current = moves[i];
        const isRocket = current.card.color === 'Rocket';
        const isBestRocket = bestMove.card.color === 'Rocket';
        if (isRocket) {
            if (!isBestRocket) bestMove = current;
            else if (current.card.value > bestMove.card.value) bestMove = current;
        } else if (current.card.color === leadColor && !isBestRocket) {
            if (current.card.value > bestMove.card.value) bestMove = current;
        }
    }
    return bestMove.player;
  };

  const startNewGame = async () => {
    console.log("startNewGame : D√©but...");
    
    // --- AJOUTER CE BLOC ---
    // Si une requ√™te est en cours, on l'annule !
    if (abortControllerRef.current) {
        abortControllerRef.current.abort();
        abortControllerRef.current = null;
    }
    // -----------------------

    setIsPlaying(false); 
    setStats(null); 
    setMissions([]); 
    setBookMissionId(0);
    setCurrentMoveIndex(-1); 
    setIsThinking(false); // Force l'arr√™t du mode "pens√©e"
    
    if (timerRef.current) clearTimeout(timerRef.current);

    try {
      const res = await fetch('http://localhost:8000/start-game');
      const gameData = await res.json();
      const generatedCards: CardData[] = [];
      [gameData.player_1, gameData.player_2, gameData.player_3, gameData.player_4].forEach((hand, playerIdx) => {
          hand.forEach((c: any) => {
              const seed = c.value * 100 + playerIdx * 50 + c.color.length;
              generatedCards.push({
                  id: `${c.color}-${c.value}`,
                  color: c.color,
                  value: c.value,
                  owner: playerIdx,
                  status: 'HAND',
                  zIndex: 0,
                  tableRotation: (seed % 40) - 20,
                  winnerIndex: undefined
              });
          });
      });
      setAllCards(generatedCards);
      addLog("Nouvelle donne pr√™te.");
    } catch (e) { addLog("Erreur Backend"); }
  };

  const launchSolver = async () => {
    console.log("launchSolver : D√©but...");
    
    // --- AJOUTER CE BLOC ---
    // On annule toute requ√™te pr√©c√©dente par s√©curit√©
    if (abortControllerRef.current) {
        abortControllerRef.current.abort();
    }
    // On cr√©e un nouveau contr√¥leur pour cette nouvelle demande
    const controller = new AbortController();
    abortControllerRef.current = controller;
    // -----------------------

    setIsThinking(true); 
    setStats(null); 
    goToStep(-1); 
    
    const handsForBackend = { player_1: [], player_2: [], player_3: [], player_4: [] } as any;
    allCards.forEach(c => { handsForBackend[`player_${c.owner + 1}`].push({ color: c.color, value: c.value }); });
    const realMissions = missions.map(m => ({ card: { color: m.cardColor, value: m.cardValue }, owner: m.ownerIndex, token: m.token }));

    try {
        console.log("Envoi au solver...", realMissions);
        const res = await fetch('http://localhost:8000/solve-game', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ ...handsForBackend, missions: realMissions }),
            
            // --- AJOUTER CECI : ---
            signal: controller.signal // On lie le signal d'annulation ici
            // ----------------------
        });
        
        if (!res.ok) throw new Error(`HTTP Error: ${res.status}`);
        const data = await res.json();
        console.log("R√©ponse solver:", data);
        setStats(data.stats);
        if(data.stats.solutionFound) addLog(`Solution trouv√©e !`); else addLog("Pas de solution trouv√©e.");
    
    } catch(e: any) { 
        // --- MODIFIER LA GESTION D'ERREUR ---
        if (e.name === 'AbortError') {
            addLog("Calcul annul√©."); // Ce n'est pas une vraie erreur
        } else {
            console.error("ERREUR SOLVER:", e);
            addLog(`Erreur Solver: ${e.message}`); 
        }
    } finally { 
        setIsThinking(false); 
        abortControllerRef.current = null; // Nettoyage
    }
  };

  const autoFindSolution = async () => {
    // Il faut avoir s√©lectionn√© une mission du livre pour savoir quelle structure chercher
    if (bookMissionId === 0) {
        addLog("‚ö†Ô∏è Veuillez s√©lectionner une mission dans le Livre de Bord d'abord.");
        return;
    }
    
    // Annulation des processus pr√©c√©dents
    if (abortControllerRef.current) abortControllerRef.current.abort();
    const controller = new AbortController();
    abortControllerRef.current = controller;

    setIsAutoFinding(true);
    addLog("üé∞ Recherche d'une donne PARFAITE (Donne + Missions)...");
    
    const MAX_ATTEMPTS = 500; // On peut tester beaucoup car c'est rapide
    let attempts = 0;
    let found = false;

    // On r√©cup√®re la d√©finition de la mission (ex: Mission 30 a 6 slots avec tokens 1,2,3,4,5,6)
    const missionDef = MISSION_LOGBOOK.find(m => m.id === bookMissionId);
    if (!missionDef) return;

    try {
        while (!found && attempts < MAX_ATTEMPTS) {
            if (controller.signal.aborted) break;
            attempts++;

            // --- 1. DEMANDER UNE NOUVELLE DONNE VIRTUELLE ---
            // On appelle /start-game pour avoir des mains toutes neuves
            const resDeal = await fetch('http://localhost:8000/start-game', { signal: controller.signal });
            if (!resDeal.ok) continue;
            const gameData = await resDeal.json();

            // --- 2. GENERER DES MISSIONS ALEATOIRES SUR CETTE DONNE ---
            // On reconstitue un deck plat pour piocher dedans
            const flatDeck: {color: string, value: number, owner: number}[] = [];
            ['player_1', 'player_2', 'player_3', 'player_4'].forEach((key, pIdx) => {
                gameData[key].forEach((c: any) => {
                    if (c.color !== 'Rocket') flatDeck.push({ ...c, owner: pIdx });
                });
            });

            // M√©lange des cartes t√¢ches
            const shuffledTasks = flatDeck.sort(() => Math.random() - 0.5);

            // Cr√©ation des missions selon la structure du Livre (Mission 30 etc)
            // On distribue al√©atoirement aux joueurs (0..3)
            const testMissions = missionDef.slots.map((slot, i) => {
                const taskCard = shuffledTasks[i];
                return {
                    card: { color: taskCard.color, value: taskCard.value },
                    owner: Math.floor(Math.random() * 4), // Propri√©taire t√¢che al√©atoire
                    token: slot.token
                };
            });

            // --- 3. ENVOI AU SOLVER ---
            // On pr√©pare le payload
            const payload = {
                player_1: gameData.player_1,
                player_2: gameData.player_2,
                player_3: gameData.player_3,
                player_4: gameData.player_4,
                missions: testMissions
            };

            const resSolve = await fetch('http://localhost:8000/solve-game', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(payload),
                signal: controller.signal
            });

            if (!resSolve.ok) continue;
            const solveData = await resSolve.json();

            // --- 4. VERIFICATION ---
            if (solveData.stats.solutionFound) {
                found = true;
                addLog(`‚úÖ CONFIGURATION GAGNANTE TROUV√âE (Essai ${attempts}) !`);
                
                // --- 5. MISE A JOUR DE L'INTERFACE ---
                // A. On affiche la nouvelle donne (cartes en main)
                const newAllCards = convertBackendToFrontend(gameData);
                setAllCards(newAllCards);

                // B. On affiche les nouvelles missions
                setMissions(testMissions.map(tm => ({
                    cardColor: tm.card.color,
                    cardValue: tm.card.value,
                    ownerIndex: tm.owner,
                    token: tm.token
                })));
                
                // C. On affiche la solution
                setStats(solveData.stats);
            } else {
                // Feedback visuel l√©ger tous les 10 essais pour dire "je bosse encore"
                if (attempts % 10 === 0) addLog(`... Essai ${attempts} √©chou√© ...`);
            }
        }

        if (!found) {
            addLog(`‚ùå Echec apr√®s ${MAX_ATTEMPTS} donnes diff√©rentes.`);
        }

    } catch (e: any) {
        if (e.name !== 'AbortError') console.error(e);
    } finally {
        setIsAutoFinding(false);
        abortControllerRef.current = null;
    }
  };

  return (
    <div className="h-screen w-screen flex overflow-hidden bg-[#2c3e50] font-sans">
      
      {/* SIDEBAR */}
      <div className="w-[20%] min-w-[250px] bg-gray-900 border-r border-gray-700 flex flex-col z-50 shadow-2xl h-full">
        
        {/* 1. HEADER FIXE (Ne bouge pas) */}
        <div className="p-4 border-b border-gray-800 shrink-0">
            <h1 className="text-xl font-bold text-white">The Crew <span className="text-purple-500">Solver</span></h1>
        </div>
        
        {/* 2. ZONE SCROLLABLE (Prend tout l'espace restant) */}
        <div className="flex-1 overflow-y-auto p-4 scrollbar-thin scrollbar-thumb-gray-700 scrollbar-track-transparent">
            
            <div className="flex flex-col gap-3 mb-6">
                <button onClick={startNewGame} className="bg-gray-800 hover:bg-gray-700 text-white py-3 rounded font-bold text-sm border border-gray-600">
                    üîÑ Nouvelle Donne
                </button>

                {/* --- ZONE GESTION DES MISSIONS --- */}
                <div className="bg-black/20 p-3 rounded border border-white/10 flex flex-col gap-3">
                    
                    {/* SELECTEUR LIVRE */}
                    <div>
                        <h3 className="text-xs font-bold text-gray-400 mb-1 uppercase flex items-center gap-2">
                            üìñ Livre de Bord
                            {bookMissionId > 0 && <span className="text-purple-400 text-[10px]">{bookMissionId}</span>}
                        </h3>
                        <select 
                            className="w-full bg-gray-800 text-white text-xs p-2 rounded border border-gray-600 outline-none focus:border-purple-500"
                            value={bookMissionId}
                            onChange={(e) => loadMissionFromBook(Number(e.target.value))}
                        >
                            <option value="0">-- Choisir une mission --</option>
                            {MISSION_LOGBOOK.map(m => (
                                <option key={m.id} value={m.id}>
                                    Mission {m.id} ({m.slots.length} cartes)
                                </option>
                            ))}
                        </select>
                        {bookMissionId > 0 && (
                         <div className="mt-2">
                             {/* Description */}
                             <p className="text-[9px] text-gray-400 italic leading-tight px-1 mb-2">
                                 {MISSION_LOGBOOK.find(m => m.id === bookMissionId)?.description}
                             </p>

                             {/* BARRE D'OUTILS DE MELANGE */}
                             <div className="flex gap-1">
                                 <button 
                                    onClick={shuffleOwners}
                                    className="flex-1 bg-gray-700 hover:bg-gray-600 text-[9px] text-white py-1 rounded border border-gray-600 active:scale-95 transition"
                                    title="Changer al√©atoirement qui doit faire les missions"
                                 >
                                    üë§ Mix Joueurs
                                 </button>
                                 <button 
                                    onClick={shuffleCards}
                                    className="flex-1 bg-gray-700 hover:bg-gray-600 text-[9px] text-white py-1 rounded border border-gray-600 active:scale-95 transition"
                                    title="Changer al√©atoirement les cartes cibles"
                                 >
                                    üÉè Mix Cartes
                                 </button>
                                 <button 
                                    onClick={autoFindSolution}
                                    disabled={isAutoFinding}
                                    className={`flex-2 py-3 rounded text-[10px] font-bold border transition flex items-center justify-center gap-2 shadow-lg ${
                                        isAutoFinding 
                                        ? 'bg-purple-900/80 border-purple-500 text-purple-200 animate-pulse cursor-wait' 
                                        : 'bg-gradient-to-r from-purple-700 to-indigo-700 hover:from-purple-600 hover:to-indigo-600 text-white border-indigo-500'
                                    }`}
                                >
                                    {isAutoFinding ? (
                                        <>üé≤ Recherche de donne gagnante...</>
                                    ) : (
                                        <>üé∞ Trouver une donne GAGNANTE</>
                                    )}
                                </button>
                             </div>
                         </div>
                    )}

                    <div className="h-[1px] bg-white/10 mt-2"></div>

                    </div>

                    {/* LISTE DES SLOTS */}
                    {/* J'ai retir√© le max-h ici car c'est toute la sidebar qui scroll maintenant */}
                    <div className="space-y-2 pr-1">
                        {missions.map((m, i) => {
                            const targetId = `${m.cardColor}-${m.cardValue}`;
                            const targetCard = allCards.find(c => c.id === targetId);
                            const isAccomplished = targetCard?.status === 'WON' && targetCard?.winnerIndex === m.ownerIndex;
                            const isFailed = targetCard?.status === 'WON' && targetCard?.winnerIndex !== undefined && targetCard?.winnerIndex !== m.ownerIndex;

                            return (
                            <div key={i} className={`p-2 rounded border transition-colors ${
                                isAccomplished ? 'bg-green-900/30 border-green-500/50' : 
                                isFailed ? 'bg-red-900/30 border-red-500/50' :
                                'bg-gray-800 border-gray-600'
                            }`}>
                                <div className="flex justify-between items-center mb-1">
                                    <div className="flex items-center gap-2">
                                        <span className="text-xs">{isAccomplished ? '‚úÖ' : isFailed ? '‚ùå' : 'üéØ'}</span>
                                        {m.token !== 'None' && (
                                            <span className="bg-purple-600 text-white px-1.5 rounded text-[9px] font-bold">
                                                {m.token === 'Omega' ? 'Œ©' : m.token}
                                            </span>
                                        )}
                                    </div>
                                    <button onClick={() => removeMission(i)} className="text-gray-500 hover:text-red-400 text-xs">‚úï</button>
                                </div>

                                <div className="flex gap-1">
                                    <select 
                                        className="bg-black/30 text-white text-[10px] p-1 rounded w-16 outline-none"
                                        value={m.ownerIndex}
                                        onChange={(e) => {
                                            const newMissions = [...missions];
                                            newMissions[i].ownerIndex = Number(e.target.value);
                                            setMissions(newMissions);
                                        }}
                                    >
                                        <option value={0}>Sud</option>
                                        <option value={1}>Ouest</option>
                                        <option value={2}>Nord</option>
                                        <option value={3}>Est</option>
                                    </select>

                                    <select 
                                        className="bg-black/30 text-white text-[10px] p-1 rounded w-10 text-center outline-none"
                                        value={m.cardValue}
                                        onChange={(e) => {
                                            const newMissions = [...missions];
                                            newMissions[i].cardValue = Number(e.target.value);
                                            setMissions(newMissions);
                                        }}
                                    >
                                        {[1,2,3,4,5,6,7,8,9].map(n => <option key={n} value={n}>{n}</option>)}
                                    </select>

                                    <select 
                                        className={`text-[10px] p-1 rounded flex-1 font-bold outline-none ${
                                            m.cardColor === 'Blue' ? 'bg-blue-900/50 text-blue-200' :
                                            m.cardColor === 'Green' ? 'bg-green-900/50 text-green-200' :
                                            m.cardColor === 'Pink' ? 'bg-pink-900/50 text-pink-200' :
                                            m.cardColor === 'Yellow' ? 'bg-yellow-900/50 text-yellow-200' : 
                                            'bg-gray-700 text-white'
                                        }`}
                                        value={m.cardColor}
                                        onChange={(e) => {
                                            const newMissions = [...missions];
                                            newMissions[i].cardColor = e.target.value;
                                            setMissions(newMissions);
                                        }}
                                    >
                                        <option value="Blue">Bleu</option>
                                        <option value="Green">Vert</option>
                                        <option value="Pink">Rose</option>
                                        <option value="Yellow">Jaune</option>
                                        <option value="Rocket">Fus√©e</option>
                                    </select>
                                </div>
                            </div>
                        )})}
                        {missions.length === 0 && <div className="text-gray-500 text-[10px] text-center py-2">Aucune mission active</div>}
                    </div>

                    <button 
                        onClick={() => setMissions(prev => [...prev, { cardColor: 'Blue', cardValue: 1, ownerIndex: 0, token: 'None' }])}
                        className="w-full border border-dashed border-gray-600 text-gray-400 hover:text-white text-[10px] py-1 rounded transition-colors hover:border-gray-400"
                    >
                        + Ajouter slot manuel
                    </button>
                </div>
                {/* --- FIN ZONE MISSION --- */}

                <button 
                    onClick={launchSolver} 
                    disabled={isThinking} 
                    className={`py-3 rounded font-bold transition text-sm ${isThinking ? 'bg-purple-900 text-purple-300' : 'bg-purple-600 hover:bg-purple-500 text-white'}`}
                >
                    {isThinking ? 'Calcul en cours...' : '‚ö°Ô∏è Lancer Solver'}
                </button>
            </div>

            {/* CONTROLES LECTURE */}
            {stats && (
                <div className="bg-black/40 p-3 rounded-lg border border-white/10 mb-4 animate-fade-in">
                    <div className="flex justify-between items-center mb-2 text-xs text-gray-400 font-mono">
                        <span>Coup {Math.ceil(currentMoveIndex + 1)} / {stats.solution_steps.length}</span>
                        <span>{isPlaying ? '‚ñ∂ LECTURE' : '‚è∏ PAUSE'}</span>
                    </div>
                    <div className="w-full h-1.5 bg-gray-700 rounded-full mb-4 overflow-hidden">
                        <div 
                            className="h-full bg-gradient-to-r from-purple-500 to-pink-500 transition-all duration-300"
                            style={{ width: `${((currentMoveIndex + 1) / stats.solution_steps.length) * 100}%` }}
                        ></div>
                    </div>
                    <div className="flex gap-2">
                        <button onClick={handlePrev} className="flex-1 bg-gray-700 hover:bg-gray-600 text-white py-2 rounded text-lg active:scale-95">‚èÆ</button>
                        <button onClick={() => setIsPlaying(!isPlaying)} className={`flex-1 py-2 rounded text-lg font-bold active:scale-95 text-white ${isPlaying ? 'bg-yellow-600 hover:bg-yellow-500' : 'bg-green-600 hover:bg-green-500'}`}>{isPlaying ? '‚è∏' : '‚ñ∂'}</button>
                        <button onClick={handleNext} className="flex-1 bg-gray-700 hover:bg-gray-600 text-white py-2 rounded text-lg active:scale-95">‚è≠</button>
                    </div>
                </div>
            )}

            {/* --- RAPPORT DE SOLUTION (NOUVEAU) --- */}
            {stats && stats.solutionFound && (
                <div className="flex-1 overflow-y-auto min-h-[150px] mb-4 pr-1 scrollbar-thin scrollbar-thumb-gray-700">
                    <TrickHistory steps={stats.solution_steps} />
                </div>
            )}

            {/* LOGS */}
            <div className="bg-black/40 rounded p-2 font-mono text-[10px] text-gray-400 border border-white/5 min-h-[100px]">
                {logs.map((l, i) => <div key={i} className="mb-1">{l}</div>)}
            </div>

        </div>
      </div>

      {/* ZONE DE JEU */}
      <div className="w-[80%] relative bg-[#2c3e50] overflow-hidden shadow-inner">
        <div className="absolute top-45 left-1/2 -translate-x-1/2 text-white/20 font-bold tracking-[0.5em]">NORD</div>
        <div className="absolute bottom-45 left-1/2 -translate-x-1/2 text-white/20 font-bold tracking-[0.5em]">SUD</div>
        <div className="absolute left-45 top-1/2 -translate-y-1/2 text-white/20 font-bold tracking-[0.5em] [writing-mode:vertical-lr] rotate-180">OUEST</div>
        <div className="absolute right-45 top-1/2 -translate-y-1/2 text-white/20 font-bold tracking-[0.5em] [writing-mode:vertical-lr]">EST</div>

        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-48 h-48 rounded-full border-2 border-white/5 bg-white/5 pointer-events-none"></div>

        {allCards.map((card) => {
            const style = getCardStyle(card);
            const speed = card.status === 'WON' ? '1000ms' : '600ms';
            return (
                <div 
                    key={card.id}
                    className="absolute top-1/2 left-1/2 transition-all ease-in-out"
                    style={{ 
                        ...style,
                        width: '5rem',
                        transitionProperty: 'transform, opacity', 
                        transitionDuration: speed
                    }}
                >
                    <Card color={card.color} value={card.value} className="w-20 aspect-[2/3] shadow-xl border border-black/20" />
                </div>
            );
        })}
      </div>
    </div>
  );
}